<template name="title-text">
	<view class="main" v-if="is">
		<view v-for="(item,index) in desc" @tap="tabEvents(index)" :class="{org:index == current}" :key="index">
			{{item.name}}
			<view class="icon-box" v-if="index==1||index==2">
				<uni-icons :color="item.selectUp=='up'?'rgb(221, 82, 77)':'#fff'" type="arrowup" size="10"></uni-icons>
				<uni-icons :color="item.selectUp=='down'?'rgb(221, 82, 77)':'#fff'" type="arrowdown" size="10"></uni-icons>
			</view>
			<view class="goods" >
				<view v-for="item in goodsList" :key="item.id">
					<image :src="item.pic" mode="widthFix" style="width: 120rpx;height: 80rpx;"></image>
					<view class="title">
						{{ item.name }}
					</view>
					<view class="price">
						{{ item.price }}
					</view>
				</view>
			</view>
		</view>
		
	</view>
	
</template>

<script>
	import {
		findProductList
	} from "../api/kind/index.js"
	import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
	export default {
		components: {
			uniLoadMore
		},
		name: 'title-text',
		data() {
			return {
				id: "",
				isDesc: '',
				is:true,
				goodsList: [],
				start: 1,
				limit: 5,
				sortBy: "",
				title:'加载中',
				total: 0,
				//当前选中的current
				current: 0,

				desc: [{
						name: '新品',
						price: '',
						desc: '',


					},
					{
						name: '销量',
						price: '',
						desc: '',
						selectUp: 'default', //默认状态 
					},
					{
						name: '价格',
						price: '',
						desc: '',
						selectUp: 'default', //默认状态 
					},
					{
						name: '筛选',
						price: '',
						desc: '',
						arow: false

					},
				]
			}
		},
		methods: {
			tabEvents(val) {
				//点击tab栏控制文字的颜色
				//控制icon字体图标颜色
				this.goodsList = [];
				this.start = 1;
				this.limit = 5;
				var item = this.desc.find((ele, index) => index == val);
				// 多次点击不需要重置
				if (this.current == val) {
					console.log(1)
				} else {
					console.log('切换');
					//重置tab栏状态
					this.desc.forEach(ele => ele.selectUp = "default");
					this.desc[val].name;
					if (this.desc[val].name == "销量") {
						this.sortBy = "sale"
					} else if (this.desc[val].name == '价格')
						this.sortBy = "price"
				}

				if (item.selectUp == 'default') {
					item.selectUp = 'up'
					this.isDesc = 1;
				} else if (item.selectUp == 'up') {
					item.selectUp = 'down';
					this.isDesc = 0
				} else {
					item.selectUp = "up";
					this.isDesc = 1;
				}
				this.current = val;
				this.init();
			},


			init() {
				findProductList(this.start, this.limit, {
					"categoryId": this.id,
					"isDesc": this.isDesc,
					"sortBy": this.sortBy
				}).then(res => {
					uni.stopPullDownRefresh();
					this.goodsList.push(...res.data.rows);
					this.total = res.data.total;
					console.log(res);
				})
			}
		},
		//下拉刷新
		onPullDownRefresh() {
			//获取最新的数据
			this.start = 1;
			this.limit = 5;
			this.goodsList = [];
			this.init()
		},
		//上拉加载
		onReachBottom() {
			//对数据总数进行判断 
			if (this.total > this.start * this.limit) {
				this.start++;
				this.init();

			} else {
				//阻止请求下一页数据
				uni.showToast({
					title: "没有更多数据了",
					icon: "none"
				})
			}
		},
		onLoad(options) {
			console.log('页面传递的参数', options.id);
			this.id = options.id;
			this.init();
		},

	}
</script>

<style scoped lang="scss">
	.main{
		display: flex;
		align-items: center;
	}


	
</style>
